@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main-header,
.main-footer {
  flex-grow: 0;
  flex-shrink: 0;
}

.main-index,
.main-inner,
.main-form {
  flex-grow: 1;
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1150px;
  margin: 0 auto;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 175px;
  padding: 15px 16px 14px 16px;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  font-family: inherit;
  text-align: center;
  color: #444444;
  text-transform: uppercase;
  background-color: #ffffff;
  border: 2px solid #444444;
}
.button:hover {
  color: #ffffff;
  background-color: #444444;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
}

.main-header {
  width: 100%;
  background-color: #f9f9f9;
}
@media (min-width: 768px) {
  .main-header {
    min-height: 89px;
    padding: 0 50px;
    overflow: hidden;
  }
}
.main-header--inner {
  background-color: #f1f1f1;
}

.main-header__nav {
  position: relative;
  z-index: 1;
  width: 100%;
  max-width: 1050px;
  margin: 0 auto;
  padding-top: 80px;
  background-color: #ffffff;
  box-shadow: inset 0 1px 0 0 #ececec, 0 1px 0 0 #ececec;
}
@media (min-width: 768px) {
  .main-header__nav {
    padding-top: 0;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    box-shadow: none;
  }
}

.main-header__logo {
  position: absolute;
  top: 22px;
  left: 50%;
  z-index: 1;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .main-header__logo {
    left: -2px;
    transform: none;
  }
}
@media (min-width: 1150px) {
  .main-header__logo {
    top: 9px;
    left: 50%;
    transform: translateX(-50%);
  }
}
.main-header__logo--index {
  pointer-events: none;
}

.main-header__logo:hover img {
  opacity: 60%;
}

.main-header__logo:active img {
  opacity: 30%;
}

.main-header__button {
  position: absolute;
  top: 32px;
  right: 30px;
  width: 21px;
  height: 20px;
  background-color: rgba(255, 255, 255, 0);
  background-image: url("../img/svg/hamburger.svg");
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
}
.no-js .main-header__button {
  display: none;
}
@media (min-width: 768px) {
  .main-header__button {
    display: none;
  }
}
.main-header__button--is-open {
  top: 29px;
  background-image: url("../img/svg/cross.svg");
}
.main-header__button:hover {
  opacity: 60%;
}
.main-header__button:active {
  opacity: 30%;
}

.main-header__nav-wrapper {
  display: none;
  grid-template-areas: "site-nav" "user-nav";
}
@media (min-width: 768px) {
  .main-header__nav-wrapper {
    display: grid;
    grid-template-areas: "user-nav user-nav" "site-nav delivery-text";
    grid-template-columns: 48% 52%;
  }
}
@media (min-width: 1150px) {
  .main-header__nav-wrapper {
    grid-template-areas: "site-nav user-nav" "news-nav delivery-text";
  }
}
.no-js .main-header__nav-wrapper {
  display: grid;
}
.main-header__nav-wrapper--nav-is-open {
  display: grid;
}
@media (max-width: 767px) {
  .main-header__nav-wrapper--nav-is-open {
    position: absolute;
    top: 80px;
    left: 0;
    width: 100%;
  }
}

.main-header__site-nav {
  position: relative;
  margin: 0;
  padding: 0;
  list-style-type: none;
  grid-area: site-nav;
}
@media (min-width: 768px) {
  .main-header__site-nav {
    display: flex;
    flex-wrap: wrap;
  }
}
@media (min-width: 1150px) {
  .main-header__site-nav {
    padding-right: 100px;
    display: grid;
    grid-template-columns: 185px auto;
  }
}

.main-header__site-nav::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100vw;
  z-index: -1;
  width: 200vw;
  height: 100%;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-header__site-nav::before {
    background-color: rgba(255, 255, 255, 0);
  }
}

.main-header__site-item {
  padding: 27px 30px 27px 76px;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .main-header__site-item {
    padding: 20px 37px 0 0;
    border: none;
  }
  .main-header__site-item:nth-child(even) {
    padding-right: 10px;
  }
}
@media (min-width: 1150px) {
  .main-header__site-item {
    padding: 34px 34px 0 0;
  }
  .main-header__site-item:nth-child(odd) {
    border-right: 1px solid #ececec;
  }
  .main-header__site-item:nth-child(even) {
    padding-left: 35px;
  }
}
.main-header__site-item:first-child {
  border-top: 1px solid #ececec;
}
@media (min-width: 768px) {
  .main-header__site-item:first-child {
    border-top: none;
  }
}

.main-header__site-link {
  display: block;
  font-weight: 700;
  color: inherit;
}
@media (min-width: 768px) {
  .main-header__site-link {
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .main-header__site-link {
    font-size: 17px;
  }
}
.main-header__site-link--no-cursor {
  pointer-events: none;
}
.main-header__site-link:hover {
  opacity: 60%;
}
.main-header__site-link:active {
  opacity: 30%;
}

.main-header__user-nav {
  position: relative;
  margin: 0;
  padding: 0;
  list-style-type: none;
  grid-area: user-nav;
}
@media (min-width: 768px) {
  .main-header__user-nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-content: flex-start;
    width: 52%;
    margin-left: auto;
  }
}
@media (min-width: 1150px) {
  .main-header__user-nav {
    width: 100%;
    padding-left: 100px;
  }
}

.main-header__user-nav::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100vw;
  z-index: -1;
  width: 200vw;
  height: 100%;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-header__user-nav::before {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }
}

.main-header__user-item {
  padding: 26px 30px 27px 30px;
  border-bottom: 1px solid #ececec;
}
.main-header__user-item--cart {
  padding: 28px 30px 26px 30px;
}
@media (min-width: 768px) {
  .main-header__user-item--search {
    padding: 36px 31px 28px 33px;
    border-right: 1px solid #ececec;
    border-left: 1px solid #ececec;
    min-height: 89px;
  }
  .main-header__user-item--cart {
    padding: 33px 0 30px 31px;
    border: none;
  }
}
@media (min-width: 1150px) {
  .main-header__user-item--search {
    padding-right: 32px;
  }
  .main-header__user-item--cart {
    padding-right: 2px;
  }
}

.main-header__user-link {
  position: relative;
  display: block;
  padding-left: 46px;
  font-weight: 700;
  color: inherit;
}
@media (min-width: 768px) {
  .main-header__user-link--search {
    padding-left: 18px;
  }
}
@media (min-width: 768px) {
  .main-header__user-link--cart {
    padding-left: 51px;
  }
}

.main-header__user-icon {
  position: absolute;
  top: 4px;
  left: 2px;
  width: 18px;
  height: 18px;
  fill: rgba(0, 0, 0, 0.2);
}
@media (min-width: 768px) {
  .main-header__user-icon {
    top: 0;
    left: 0;
  }
}
.main-header__user-icon--cart {
  top: 2px;
  left: 0;
  width: 21px;
}
@media (min-width: 768px) {
  .main-header__user-icon--cart {
    margin-right: 30px;
  }
}

@media (min-width: 768px) {
  .main-header__user-text--search {
    display: none;
  }
}

.main-header__user-link:hover .main-header__user-text {
  opacity: 60%;
}

.main-header__user-link:active .main-header__user-text {
  opacity: 30%;
}

.main-header__user-link:hover .main-header__user-icon {
  fill: #63d1bb;
}

.main-header__user-link:active .main-header__user-icon {
  fill: rgba(99, 209, 187, 0.2);
}

.main-header__news-nav {
  display: none;
  margin: 0;
  padding: 0;
  list-style-type: none;
  grid-area: news-nav;
}
@media (min-width: 1150px) {
  .main-header__news-nav {
    display: flex;
    flex-wrap: wrap;
  }
}

.main-header__news-item {
  margin-right: 62px;
  padding-top: 19px;
}
.main-header__news-item:nth-child(even) {
  margin: 0;
}

.main-header__news-link {
  display: block;
  font-weight: 700;
  font-size: 15px;
  color: inherit;
}
.main-header__news-link:hover {
  opacity: 60%;
}
.main-header__news-link:active {
  opacity: 30%;
}

.main-header__delivery-text {
  display: none;
  margin: 0;
  font-size: 15px;
  color: #444444;
  grid-area: delivery-text;
}
@media (min-width: 768px) {
  .main-header__delivery-text {
    display: block;
    padding: 20px 0 20px 50px;
    text-align: right;
  }
}
@media (min-width: 1150px) {
  .main-header__delivery-text {
    padding-right: 1px;
  }
}

.hero {
  position: relative;
  background-color: #f9f9f9;
  background-image: url("../img/background/hero-mobile.png");
  background-repeat: no-repeat;
  background-position: 50% -18px;
}
@media (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/background/hero-mobile@2x.png");
    background-size: 320px auto;
  }
}
@media (min-width: 768px) {
  .hero {
    background-image: url("../img/background/hero-tablet.png");
    background-position: 50% -65px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/background/hero-tablet@2x.png");
    background-size: 768px auto;
  }
}
@media (min-width: 1150px) {
  .hero {
    background-image: url("../img/background/hero-desktop.png");
  }
}
@media (min-width: 1150px) and (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/background/hero-desktop@2x.png");
    background-size: 1150px auto;
  }
}
