@media (min-width: 768px) {
  .hero__container {
    padding: 0 50px;
  }
}

.hero__title {
  margin: 0;
  padding: 72px 30px 120px 30px;
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  text-align: center;
}
@media (min-width: 768px) {
  .hero__title {
    padding-top: 90px;
    padding-bottom: 372px;
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .hero__title {
    padding-top: 93px;
    padding-bottom: 355px;
    font-size: 53px;
    line-height: 57px;
  }
}

@media (min-width: 768px) {
  .hero__title br {
    display: none;
  }
}

@media (min-width: 1150px) {
  .hero__title br:last-child {
    display: inline;
  }
}

@media (min-width: 768px) {
  .hero__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.hero__link {
  position: relative;
  display: block;
  padding: 24px 100px 31px 30px;
  font-weight: 700;
  font-size: 19px;
  color: #ffffff;
}
@media (min-width: 768px) {
  .hero__link {
    padding: 25px 100px 34px 36px;
    font-size: 20px;
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .hero__link {
    padding: 25px 100px 34px 37px;
  }
}
.hero__link:hover, .hero__link:active {
  margin-top: -5px;
  padding-bottom: 36px;
